import VueIcon from '@/libs/icons-vue';
import moment from 'moment';
import zhCn from './moment-zh_CN';
import { App } from 'vue';
import Button from './button';
import Affix from './affix';
import Tree from './tree';
import Cascader from './cascader';
import Calendar from './calendar';
import Select from './select';
import Card from './card';
import Dropdown from './dropdown';
import Breadcrumb from './breadcrumb';
import Checkbox from './checkbox';
import DatePicker from './date-picker';
import Grid from './grid';
import Layout from './layout';
import Icon from './icon';
import Input from './input';
import Menu from './menu';
import Modal from './modal';
import Pagination from './pagination';
import Progress from './progress';
import Radio from './radio';
import Switch from './switch';
import Tabs from './tabs';
import Tag from './tag';
import Upload from './upload';
// moment
if (window.moment) {
  window.moment.locale('zh-cn', zhCn);
} else {
  moment.locale('zh-cn', zhCn);
}
if (window.AntDesignIcons) {
  //  ant design icons, 721icon
  const icons = window.AntDesignIcons;
  Object.keys(icons).forEach(icon => {
    VueIcon.add(icons[icon]);
  });
}
// 
const components = [
  Button,
	Affix,
	Tree,
	Cascader,
	Calendar,
	Select,
	Card,
	Dropdown,
	Breadcrumb,
	Checkbox,
	DatePicker,
	Grid,
	Layout,
	Icon,
	Input,
	Menu,
	Modal,
	Pagination,
	Progress,
	Radio,
	Switch,
	Tabs,
	Tag,
	Upload
];

//  install  Vue  use 
const install = function(app: App) {
  // 
  // if (install.installed) return
  // 
  components.forEach(comp => app.use(comp));
};

export {
  // 
  Button,
	Affix,
	Tree,
	Cascader,
	Calendar,
	Select,
	Card,
	Dropdown,
	Breadcrumb,
	Checkbox,
	DatePicker,
	Grid,
	Layout,
	Icon,
	Input,
	Menu,
	Modal,
	Pagination,
	Progress,
	Radio,
	Switch,
	Tabs,
	Tag,
	Upload
};
export default {
  //  install Vue.use() 
  install,
  Button,
	Affix,
	Tree,
	Cascader,
	Calendar,
	Select,
	Card,
	Dropdown,
	Breadcrumb,
	Checkbox,
	DatePicker,
	Grid,
	Layout,
	Icon,
	Input,
	Menu,
	Modal,
	Pagination,
	Progress,
	Radio,
	Switch,
	Tabs,
	Tag,
	Upload
};

